<template>
	<div class="container">
		<div class="banner" v-if="typeArr.indexOf(1) != -1">
			<ul class="bubble">
				<li v-for="(item,index) in bubbleList" :key="index">
					<div class="bubble_box" @click="bubbleClick(index)" v-show="item.state != 1">
						<img src="../static/img/icon_bubble.png" alt="">
					</div>
				</li>
			</ul>
			<p class="welfare">我的积分:{{ userLuckyValue.amount }}</p>
			<div class="btn_signin" @click="fnSignin"></div>
			<div class="btn_personal" @click="toPath('personal')"></div>
			<div class="btn_withdrawal" @click="toWithdrawal"></div>
			<img src="../static/img/gif_fox.gif" alt="" class="gif_fox">
		</div>
		<div class="content">
			<div class="red" v-if="typeArr.indexOf(3) != -1">
				<!-- <div class="count" v-if="countFlag && typeArr.indexOf(3) != -1">
					<div class="count_down">下一波倒计时：<div><van-count-down :time="time" /></div></div>
				</div> -->
				<div class="red_count">
					<van-count-down :time="time" @finish="finish">
					  <template #default="timeData">
					    <span class="block">{{ timeData.hours > 9 ? timeData.hours : '0'+timeData.hours}}</span>
					    <span class="block">{{ timeData.minutes > 9 ? timeData.minutes : '0'+timeData.minutes }}</span>
					    <span class="block">{{ timeData.seconds > 9 ? timeData.seconds : '0'+timeData.seconds }}</span>
					  </template>
					</van-count-down>
				</div>
				<div class="red_packets">
					<div class="red_packets_box" v-for="(item,index) in redPacketsList" :key="item.id">
						<img :src="item.state == 0?notOpenGif:item.state == 2?openGif:''" alt=""
							v-show="item.state != 1" @click="redPacketsClick(index)">
					</div>
				</div>
			</div>
			<div class="freeCollar" v-if="typeArr.indexOf(15) != -1">
				<p class="freeCollar_title">精彩豪礼等你拿</p>
				<ul class="freeCollar_list">
					<li v-for="(item,index) in freeCollarList" :key="index" @click="toPath('/freeAccess')">
						<img :src="item.goodsUrl" class="freeCollar_list_img"/>
						<p class="goodsName">{{ item.goodsName }}</p>
						<p class="goodsOriginalPrice">{{ '￥'+item.goodsOriginalPrice }}</p>
						<p class="freeCollar_list_btn">0元免费领</p>
					</li>
				</ul>
				<p class="btn_freeCollar" @click="toPath('/freeAccess')"></p>
			</div>
			<div class="special_task">
				<div class="special_task_box" v-for="(item,index) in specialTaskList" :key="item.id" v-show="typeArr.indexOf(item.type) != -1" @click="toPath(item.path)">
					<img :src="item.imgUrl" alt="">
				</div>
			</div>
			<div class="general_task" v-if="typeArr.indexOf(4) != -1">
				<p class="general_task_title">日常任务<span>完成每日任务，即可获得海量奖励</span></p>
				<div class="general_task_box" v-for="(item,index) in generalTaskList" :key="item.taskId">
					<img :src="item.taskIconUrl" alt="" class="icon_general_task">
					<div class="general_task_middle">
						<p :class="'general_task_explain'+' '+'color_'+item.taskType">{{ item.taskName }}<span><i class="icon_gold"></i>+{{ item.amount }}</span></p>
						<div class="progress" v-if="item.taskType == 3 || item.taskType == 4">
							<span class="progress_portion"
								:style="'width:'+item.completedCount/item.taskCount*100+'%'"></span>
							<span class="progress_text">{{ item.completedCount + '/' +item.taskCount }}</span>
						</div>
						<p :class="'general_task_explain_text'+' '+'color_'+item.taskType" v-else>{{ item.taskExplain }}</p>
					</div>
					<div class="general_task_right">
						<p class="btn_task" @click="goTask(index)">{{ item.state == 0 ? '开始任务': item.state == 1?'进行中':item.state == 3?'已完成':'审核中'}}</p>
					</div>
				</div>
			</div>
		</div>
		<div v-show="adLoadingFlag" class="shadow">
			<van-loading size="36px" vertical text-color="#ffffff" color="#ffffff">加载中...</van-loading>
		</div>
		<signin :item="signinItem" :nowState="nowState" @clouseSigninPopup="clouseSigninPopup" v-if="signinFlag && typeArr.indexOf(2) != -1" />
		<firstPopup :item="firstPopupItem" @clouseFirstPopup="clouseFirstPopup" v-if="firstPopupFlag && typeArr.indexOf(13) != -1" />
		<popup :item="popupItem" @clousePopup="clousePopup" v-if="popupFlag" />
	</div>

</template>

<script>
	import anime from '../assets/js/anime.min.js';
	import dsbridge from '../assets/js/dsbridge.js';
	import signin from '../components/signin.vue'
	import firstPopup from '../components/firstPopup.vue'
	import popup from '../components/popup.vue'
	import countdown from '../components/countDown.vue'
	const h5PushVersion = 102;
	export default {
		name: 'Home',
		data() {
			return {
				obj:{
					"channel": "1375068375243427840",
					"appVersion": "1.0",
					"pushVersion": 1,
					"deviceType": 1,
					"osType": 1,
					"osVersion": "10",
					"mac": "94:87:E0:1C:CA:02",
					"androidId": "f34ffc82d88e54fb",
					"model": "MI 8",
					"vendor": "Xiaomi",
					"screenWidth": 1080,
					"screenHeight": 2029,
					"screenType": 1,
					"brand": "Xiaomi",
					"screenDensity": "440",
					"screenSize": "6.20",
					"ip": "192.168.1.48",
					"connectionType": 100,
					"operatorType": 0,
					"coordinateType": 1,
					"screenBrightness": 212,
					"hardware": "qcom",
					"battery": 93,
					"bssid": "02:00:00:00:00:00",
					"mno": "OTHER",
					"seriaIno": "unknown",
					"isProxy": 0,
					"chargeType": 2,
					"locale": "CN",
					"board": "sdm845",
					"cpuAbi": "arm64-v8a,armeabi-v7a,armeabi,",
					"buildId": "QKQ1.190828.002",
					"permissions": "android.permission.READ_EXTERNAL_STORAGE,android.permission.WRITE_EXTERNAL_STORAGE,android.permission.ACCESS_FINE_LOCATION,android.permission.ACCESS_NETWORK_STATE,android.permission.BLUETOOTH,android.permission.INTERNET,android.permission.READ_PRIVILEGED_PHONE_STATE,com.asus.msa.SupplementaryDID.ACCESS,freemme.permission.msa,android.permission.READ_PHONE_STATE,android.permission.ACCESS_WIFI_STATE,android.permission.ACCESS_COARSE_LOCATION,android.permission.WAKE_LOCK,android.permission.BROADCAST_STICKY,android.permission.REQUEST_INSTALL_PACKAGES,android.permission.CHANGE_NETWORK_STATE,android.permission.QUERY_ALL_PACKAGES,com.kl.zjhb.openadsdk.permission.TT_PANGOLIN,com.kl.zjhb.permission.KW_SDK_BROADCAST,android.permission.CHANGE_WIFI_STATE,",
					"isScreenOn": true,
					"isUnlock": false,
					"uuid": "41ecd798-9335-3bdf-8351-24ff37dd07fb",
					"installPkg": "com.kl.zjhb",
					"updateType": "SDK",
					"userId": "wsws",//sth15861694662
					"version": "100",
					"mediaCode": "1375068375243427840",
					"appId": "1375068375243427840"
				},
				adLoadingFlag:false,
				nowType:'',//展示激励视频所用type
				nowIndex:0,//展示激励视频所用index
				typeArr:[],
				time:0,
				countFlag:true,
				signinFlag:false,
				firstPopupFlag:false,
				popupFlag:false,
				newUser:false,
				popupItem:{},
				signinItem:{},
				nowState:true,
				firstPopupItem:{},
				openGif: require('../static/img/gif_open.gif'),
				notOpenGif: require('../static/img/gif_not_open.gif'),
				redPacketsList: [],
				bubbleList:[],
				userLuckyValue:{},
				specialTaskList: [{
						id: 0,
						name: '充话费',
						imgUrl: require('../static/img/img_task01.png'),
						type:14,
						path:'payPhone'
					},
					{
						id: 1,
						name: '电影票',
						imgUrl: require('../static/img/img_task02.png'),
						type:11,
					},
					{
						id: 2,
						name: '读小说',
						imgUrl: require('../static/img/img_task03.png'),
						type:9,
					},
					{
						id: 3,
						name: '视频赚',
						imgUrl: require('../static/img/img_task04.png'),
						type:7,
					},
					{
						id: 4,
						name: '微信阅读',
						imgUrl: require('../static/img/img_task05.png'),
						type:5,
					},
					{
						id: 5,
						name: '游戏试玩',
						imgUrl: require('../static/img/img_task06.png'),
						type:6,
					},
					{
						id: 6,
						name: '咨询赚',
						imgUrl: require('../static/img/img_task07.png'),
						type:12,
						path:'informationPage'
					},
				],
				generalTaskList: [],
				jlsAdList:[],
				lyh5xReadyFlag:true,
				codeIdList:[],
				freeCollarList:[]
			}
		},
		components:{
			signin,
			firstPopup,
			popup,
			countdown
		},
		mounted() {
			let _this = this;
			if(_this.lyh5xReadyFlag){
				if (window.lyh5x) {
					console.log('if');
				    _this.lyh5xReady();
				} else {
					console.log('else');
				    document.addEventListener('lyh5xready',_this.lyh5xReady,false);
				}
			}else{
				_this.getMediaModularConf();//获取模块配置
				_this.getRedPacketData();//获取红包数据
				_this.getTopData();////获取气泡数据
				_this.getUserLuckyValue();//获取福利值
				_this.getTaskData();//获取任务数据
				_this.getHomePageCommodity();//获取免费领分类
			}
		},
		methods: {
			init(){
				var init = localStorage.getItem("init");
				if(!init){
					let _this = this;
					var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
					_this.$axios({
					  method: 'post',
					  url: '/init',
					  data:data,
					}).then(function(res) {
					  if(res.data.code === 200){
						localStorage.setItem("init","true");
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log('init=-======',reData);
						const backButton = reData.backButton;
						localStorage.setItem("backButton",backButton);
					  }
					});
				}
				
			},
			finish(){
				let _this = this;
				var timeStamp = new Date();
				timeStamp.setHours(0,0,0,0);
				var nowTimeStamp = new Date().getTime();
				var tomorrow = timeStamp.getTime() + 86400000;
				_this.time = tomorrow-nowTimeStamp;
			},
			getUserLuckyValue(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/getUserLuckyValue',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					_this.userLuckyValue = reData;
					_this.$store.commit('increment',(reData.amount - _this.$store.state.amount));
				
				  }
				});
			},
			isNewUser(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/isNewUser',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log('isNewUser========',reData)
					if(reData.isNewUser){
						_this.loadRewardVideoAd(reData.slotId);
						_this.firstPopupItem = reData;
						_this.firstPopupFlag = true;
					}
					_this.sign();//签到接口
				  }
				});
			},
			getRedPacketData(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/getRedPacketData',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					_this.redPacketsList = reData;
					_this.countDown();
					setTimeout(function(){
						for(var i in reData){
							_this.loadRewardVideoAd(reData[i].redPacketSlotId);
						}
					},500)
					
				  }
				});
			},
			redPacketsClick(i) {
				let _this = this;
				_this.rewardVideoAdShow(i,'redPackets',_this.redPacketsList[i].redPacketSlotId);
			},
			receiveRedPacket(isDouble,redPacketId,i){//领取红包
				let _this = this;
				var exclusiveObj = {
					isDouble:isDouble,//是否翻倍 0 否 1 是
					redPacketId:redPacketId,//红包id，红包相关必传
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/receiveRedPacket',
					data:data,
				}).then(function(res){
					if(res.data.code === 200){
						_this.addWelfare(isDouble == 0? _this.redPacketsList[i].amount:_this.redPacketsList[i].doubleAmount);
						_this.redPacketsList[i].state = 1;
						_this.countDown();
					}
				})
			},
			countDown(){//红包倒计时
			let _this = this;
				var conunt = 0;
				for(var j in _this.redPacketsList){
					if(_this.redPacketsList[j].state == 1){
						conunt ++;
					}
				}
				var timeStamp = new Date();
				timeStamp.setHours(0,0,0,0);
				var nowTimeStamp = new Date().getTime();
				var tomorrow = timeStamp.getTime() + 86400000;
				if(conunt == _this.redPacketsList.length){//所有红包已被领取
					_this.time = tomorrow-nowTimeStamp;
					_this.countFlag = true;
				}
			},
			getTopData(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/getTopData',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					_this.bubbleList = reData;
					
					setTimeout(function(){
						for(var i in reData){
							_this.loadRewardVideoAd(reData[i].slotId);
						}
					},500)
					setTimeout(function(){
						anime({
							targets: '.bubble li',
							translateY: -10,
							direction: 'alternate',
							easing: 'linear',
							loop: true,
							duration: 1500
						});
					},1000)
				  }
				});
			},
			bubbleClick(i) {
				let _this = this;
				_this.rewardVideoAdShow(i,'bubble',_this.bubbleList[i].slotId);
			},
			topReceiveReward(isDouble,sort,i){//领取气泡奖励
				let _this = this;
				var exclusiveObj = {
					isDouble:isDouble,//是否翻倍 0 否 1 是
					sort:sort,//红包id，红包相关必传
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/topReceiveReward',
					data:data,
				}).then(function(res){
					if(res.data.code === 200){
						_this.bubbleList[i].state = 1;
						_this.addWelfare(isDouble == 0? _this.bubbleList[i].amount:_this.bubbleList[i].doubleAmount);
						var time = _this.bubbleList[i].timeInterval*60*1000+30000;
						setTimeout(function(){
							_this.getTopData();
						},time)
					}
				})
			},
			receiveTask(index,slotId,taskId){
				let _this = this;
				var osType;
				if(_this.isIos()){
					osType = 2;
				}else{
					osType = 1;
				}
				var exclusiveObj = {
					osType:osType,
					taskId:taskId,
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/receiveTask',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						if(_this.generalTaskList[index].taskType == 3){
							_this.rewardVideoAdShow(index,'task',slotId);
						}
					}
				})
			},
			addCompletedCount(taskId){
				let _this = this;
				var osType;
				if(_this.isIos()){
					osType = 2;
				}else{
					osType = 1;
				}
				var exclusiveObj = {
					osType:osType,
					taskId:taskId,
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/addCompletedCount',
					data:redata,
				}).then(function(res){
					_this.getTaskData();
				})
			},
			clousePopup(data){//关闭红包/气泡弹窗/任务
				let _this = this;
				_this.popupFlag = false;
				if(data.type == 'redPackets'){
					var w;
					for(var i in _this.redPacketsList){
						if(_this.redPacketsList[i].id == data.id){
							w = i;
						}
					}
					_this.receiveRedPacket(data.isDouble,data.id,w);
				}else if(data.type == 'bubble'){
					var w;
					for(var i in _this.bubbleList){
						if(_this.bubbleList[i].sort == data.id){
							w = i;
						}
					}
					_this.topReceiveReward(data.isDouble,data.id,w);
				}else if(data.type == 'task'){
					var w;
					for(var i in _this.generalTaskList){
						if(_this.generalTaskList[i].taskId == data.id){
							w = i;
						}
					}
					_this.taskReceiveReward(data.id,w);
				}else if(data.type == 'popup'){
					_this.popupFlag = false;
				}
			},
			clouseFirstPopup(data){
				let _this = this;
				var exclusiveObj = {
					amount:data.amount
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
				  method: 'post',
				  url: '/newUserReward',
				  data:redata,
				}).then(function(res) {
				  if(res.data.code === 200){
					_this.firstPopupFlag = false;
					_this.nowState = true;
					_this.getSignData(1);
					if(data.amount != -1){
						_this.addWelfare(data.amount);
					}
				  }
				});
			},
			getSignData(type){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/getSignData',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					_this.signinItem = reData;
					if(type == 1){
						_this.signinFlag = true;
					}
				  }
				});
			},
			sign(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/sign',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					_this.nowState = true;
					if(_this.firstPopupFlag){
						
					}else{
						_this.getSignData(1);
					}
				  }else{
					_this.nowState = false;
					_this.getSignData(0);
				  }
				});
			},
			fnSignin(){
				let _this = this;
				_this.getSignData(1);
			},
			clouseSigninPopup(data){
				let _this = this;
				if(data.isDouble == 3){
					_this.signinFlag = false;
				}else{
					var exclusiveObj = {
						isDouble:data.isDouble
					};
					var requestData = Object.assign(_this.obj,exclusiveObj);
					var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
					_this.$axios({
					  method: 'post',
					  url: '/signReward',
					  data:redata,
					}).then(function(res) {
					  if(res.data.code === 200){
						  _this.signinFlag = false;
						  _this.nowState = false;
						if(data.isDouble == 1){
							_this.addWelfare(data.doubleAmount);
							localStorage.setItem("signinAmount",data.doubleAmount);
							_this.popupItem = {
								type:'sPopup',
								doubleAmount:data.doubleAmount,
							};
							_this.popupFlag = true;
						}else if(data.isDouble == 0){
							_this.addWelfare(data.signAmount);
							localStorage.setItem("signinAmount",data.signAmount);
						}
					  }
					});
				}
				
			},
			addWelfare(number){//增加福利值
				let _this = this;
				_this.$store.commit('increment',number);
				anime({
				  targets: _this.userLuckyValue,
				  amount: _this.userLuckyValue.amount+number,
				  easing: 'linear',
				  round: 1,
				  update: function() {
				    
				  }
				});
			},
			toWithdrawal(){
				this.$router.push({path:'/pointsMall'});
			},
			getMediaModularConf(){
				let _this = this;
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/getMediaModularConf',
				  data:redata,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					var typeArr = [];
					for(var w in reData){
						typeArr.push(reData[w].type);
					}
					_this.typeArr = typeArr;
				  }
				});
			},
			getTaskData(){
				let _this = this;
				var osType;
				if(_this.isIos()){
					osType = 2;
				}else{
					osType = 1;
				}
				var exclusiveObj = {
					osType:osType
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
				  method: 'post',
				  url: '/getTaskData',
				  data:redata,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					_this.generalTaskList = reData;
					setTimeout(function(){
						for(var i in reData){
							if('slotId' in reData[i]){
								_this.loadRewardVideoAd(reData[i].slotId)
							}
						}
					},500)
				  }
				});
			},
			goTask(i){
				let _this = this;
				var item = _this.generalTaskList[i];
				_this.taskItem = item;
				switch (item.taskType){
					case 1:
					break;
					case 2:
					break;
					case 3:
						if(item.state == 0){
							_this.receiveTask(i,item.slotId,item.taskId);
						}else if(item.state == 1){
							_this.rewardVideoAdShow(i,'task',item.slotId);
						}
					break;
					case 4:
					break;
					case 5:
						//拍照
						if(item.state == 0){
							_this.receiveTask(i,item.slotId,item.taskId);
						}
						let params1 = {
							type:item.taskType
						}
						const str1 = JSON.stringify(params1);
						dsBridge.call("lyh5x.customtask.call", str1,_this.taskCallback);
						
					break;
					case 6:
						//分享
						if(item.state == 0){
							_this.receiveTask(i,item.slotId,item.taskId);
						}
						let params2 = {
							type:item.taskType
						}
						const str2 = JSON.stringify(params2);
						dsBridge.call("lyh5x.customtask.call", str2,_this.taskCallback);
						
					break;
					case 7:
						//邀请
						if(item.state == 0){
							_this.receiveTask(i,item.slotId,item.taskId);
						}
						let params3 = {
							type:item.taskType
						}
						const str3 = JSON.stringify(params3);
						dsBridge.call("lyh5x.customtask.call", str3,_this.taskCallback);
					break;
					default:
						console.log(item.taskType)
				}
				
			},
			taskCallback(e){
				console.log(e);
				let _this = this;
				if(e.event == 'onSuccess'){
					_this.popupItem = _this.taskItem;
					_this.popupFlag = true;
				}else if(e.event == 'onError'){
					
				}else if(e.event == 'onFail'){
					
				}
				
			},
			taskReceiveReward(id,w){
				let _this = this;
				var exclusiveObj = {
					taskId:id,
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/taskReceiveReward',
					data:data,
				}).then(function(res){
					if(res.data.code === 200){
						_this.addWelfare(_this.generalTaskList[w].amount);
						_this.getTaskData();
					}
				})
			},
			isIos(){
				var UA = navigator.userAgent;
				if (UA.match(/iPad/) || UA.match(/iPhone/) || UA.match(/iPod/)) {
					return true;
				}else{
					return false;
				}
			},
			toPath(path){
				this.$router.push({path:path});
			},
			loadRewardVideoAd(code_id){//预加载激励视频
				let _this = this;
				if(code_id != ''){
					if(_this.codeIdList.indexOf(code_id) == -1){
						_this.codeIdList.push(code_id);
						var param = { code_id: code_id };
						dsBridge.call("lyh5x.fad.cachereward.load", param);
					}
					
				}else{
					console.log('code_id为空===',code_id);
				}
				
			},
			rewardVideoAdShow(i,type,code_id){//显示激励视频
				let _this = this;
				_this.adLoadingFlag = true;
				_this.nowType = type;
				_this.nowIndex = i;
				var param = { code_id: code_id };
				dsBridge.call("lyh5x.fad.cachereward.show",param,_this.neli);
			},
			lyh5xReady() {
				let _this = this;
				if(_this.lyh5xReadyFlag){
					let obj = {
						mediaCode:lyh5x.appId,
						userId:lyh5x.userId,
						h5PushVersion:h5PushVersion,
						appVersion: dsBridge.call("lyh5x.device.appVersion"),
						battery: dsBridge.call("lyh5x.device.battery"),
						brand: dsBridge.call("lyh5x.device.brand"),
						connectionType: dsBridge.call("lyh5x.device.connectionType"),
						deviceType: dsBridge.call("lyh5x.device.deviceType"),
						hardware: dsBridge.call("lyh5x.device.hardware"),
						idfa: dsBridge.call("lyh5x.device.idfa"),
						idfv: dsBridge.call("lyh5x.device.idfv"),
						imsi: dsBridge.call("lyh5x.device.imsi"),
						ip: dsBridge.call("lyh5x.device.ip"),
						language: dsBridge.call("lyh5x.device.language"),
						manufacturer: dsBridge.call("lyh5x.device.manufacturer"),
						mno: dsBridge.call("lyh5x.device.mno"),
						model: dsBridge.call("lyh5x.device.model"),
						operatorType: dsBridge.call("lyh5x.device.operatorType"),
						osType: dsBridge.call("lyh5x.device.osType"),
						osVersion: dsBridge.call("lyh5x.device.osVersion"),
						packageName: dsBridge.call("lyh5x.device.packageName"),
						pushVersion: dsBridge.call("lyh5x.device.pushVersion"),
						screenBrightness: dsBridge.call("lyh5x.device.screenBrightness"),
						screenDensity: dsBridge.call("lyh5x.device.screenDensity"),
						screenHeight: dsBridge.call("lyh5x.device.screenHeight"),
						screenSize: dsBridge.call("lyh5x.device.screenSize"),
						screenType: dsBridge.call("lyh5x.device.screenType"),
						screenWidth: dsBridge.call("lyh5x.device.screenWidth"),
						udid: dsBridge.call("lyh5x.device.udid"),
						usbState: dsBridge.call("lyh5x.device.usbState"),
						uuid: dsBridge.call("lyh5x.device.uuid"),
						vendor: dsBridge.call("lyh5x.device.vendor"),
					}
					_this.obj = obj;
					var jsonObj = JSON.stringify(obj);
					localStorage.setItem("obj",jsonObj);
					_this.init();//初始化
					_this.isNewUser();//判断是否是新用户
					_this.getMediaModularConf();//获取模块配置
					_this.getRedPacketData();//获取红包数据
					_this.getTopData();////获取气泡数据
					_this.getUserLuckyValue();//获取福利值
					_this.getTaskData();//获取任务数据
					_this.getHomePageCommodity();//获取免费领分类
					// 在这里调用lyh5x api
					var htmlFontSize = document.getElementsByTagName('html')[0].style.fontSize.replace('px','');
					document.documentElement.style.setProperty('--barHeight', lyh5x.status_bar_height/htmlFontSize+'rem');
					_this.lyh5xReadyFlag = false;
				}
			},
			neli(ret) {
				let _this = this;
				console.log('ret====',ret);
				if (ret.event == "onError") {
					console.log('onError====',ret);
					_this.adLoadingFlag = false;
					_this.$toast('加载失败，请稍后重试···')
				} else if (ret.event == "onAdLoad") {
					console.log('onAdLoad====',ret);
				} else if (ret.event == "onRenderSuccess") {
					console.log('onRenderSuccess====',ret);
				} else if (ret.event == "onRenderFail") {
					console.log('onRenderFail====',ret);
				} else if (ret.event == "onAdShow") {
					console.log('onAdShow=====',ret);
					_this.adLoadingFlag = false;
				} else if (ret.event == "onAdClicked") {
					console.log('onAdClicked====',ret);
				}else if (ret.event == "onAdClosed") {
					console.log('onAdClosed====',ret);
					_this.adLoadingFlag = false;
				} else if (ret.event == 'onReward'){
					console.log('onReward====',ret);
					var track_id = ret.track_id;//类型
					switch (_this.nowType){
						case 'bubble':
							_this.popupItem = _this.bubbleList[_this.nowIndex];
							_this.popupFlag = true;
						break;
						case 'redPackets':
							_this.redPacketsList[_this.nowIndex].state = 2;
							_this.popupItem = _this.redPacketsList[_this.nowIndex];
							_this.popupFlag = true;
						break;
						case 'task':
						if(_this.generalTaskList[_this.nowIndex].completedCount < _this.generalTaskList[_this.nowIndex].taskCount){
							_this.addCompletedCount(_this.generalTaskList[_this.nowIndex].taskId)
						} 
						if(_this.generalTaskList[_this.nowIndex].completedCount+1 == _this.generalTaskList[_this.nowIndex].taskCount){
							_this.popupItem = _this.generalTaskList[_this.nowIndex];
							_this.popupFlag = true;
						}
						break;
					}
				}
			},
			getHomePageCommodity(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
					method: 'post',
					url: '/freebase/getHomePageCommodity',
					data:data,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						_this.freeCollarList = reData;
					}
				})
			}
		}
	}
	
	
