import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = () => new Vuex.Store({
  state: {
    amount: 0,
	readCont:0,
	infoCount:0,
	redRewardCount:10,
	redRewardAmount:10,
	bond:{
		amount:0,
		id:''
	}
  },
  mutations: {
    increment (state,number) {
      state.amount += number;
    },
	readArticle(state,number){
		state.readCont += number;
	},
	clickArticle(state,number){
		state.infoCount += number;
	},
	postRedRewardCount(state,number){
		state.redRewardCount = number;
	},
	postRedRewardAmount(state,number){
		state.redRewardAmount = number;
	},
	selectBond(state,obj){
		state.bond = obj;
	}
  }
})

export default store