(function (root, factory) {
    root.byte256 = factory(root)
})(window, function (root) {
    return root.byte256 || (function () {
        function absIndex(b) {
            return b < 0 ? b + 256 : b
        }
        
        function bytesCode(key, data) {
            for (var i = 0; i < data.length; i++) {
                var j = absIndex(data[i])
                data[i] = key[j]
            }
        }
        
        function reverse(key) {
            var reverse_key = new Array(256)
            for (var i = 0; i < 256; i++) {
                var j = absIndex(key[i])
                reverse_key[j] = i
            }
            return reverse_key
        }
        
        function uint8arrayToBase64(u8Arr) {
            let CHUNK_SIZE = 0x8000
            //arbitrary number
            let index = 0
            let length = u8Arr.length;
            let result = ''
            let slice
            while (index < length) {
                slice = u8Arr.subarray(index, Math.min(index + CHUNK_SIZE, length))
                result += String.fromCharCode.apply(null, slice)
                index += CHUNK_SIZE
            }
            return btoa(result)
        }
        
        function base64ToUint8Array(base64String) {
            let padding = '='.repeat((4 - base64String.length % 4) % 4)
            let base64 = (base64String + padding).replace(/\-/g, '+').replace(/_/g, '/')
        
            let rawData = window.atob(base64);
            let outputArray = new Uint8Array(rawData.length);
        
            for (var i = 0; i < rawData.length; ++i) {
                outputArray[i] = rawData.charCodeAt(i)
            }
            return outputArray
        }
        
        function encode(enKey, str) {
            var bytes = new TextEncoder("utf-8").encode(str)
            bytesCode(enKey, bytes)
            return uint8arrayToBase64(bytes);
        }
        
        function decode(deKey, str) {
            var bytes = base64ToUint8Array(str)
            bytesCode(deKey, bytes)
            return new TextDecoder().decode(bytes)
        }

        var byte256 = {}
        byte256.encode = encode
        byte256.decode = decode
        byte256.reverse = reverse
        return byte256
    })()
})

export default {
	byte256
}