<template>
  <div class="firstPopup">
    <div :class="state?'firstPopup_box':'firstPopup_box act'">
      <p class="cumulative" v-if="state">仅限本次</p>
      <p class="integral"><span class="text1">+</span>{{ item.amount }}<span class="text1">积分</span></p>
      <div class="btn_box2" v-if="state">
        <p class="btn_video" @click="watchBanner"></p>
        <p class="cumulative1" @click="clousePopup(-1)">我想先逛逛</p>
      </div>
      <div class="btn_box2" v-else>
        <p class="btn_goSignin" @click="clousePopup(1)"></p>
        <p class="cumulative">连续签到可获得更多积分</p>
      </div>
    </div>
	<!-- <i class="icon_close" v-if="state" @click="clousePopup(-1)"></i> -->
  </div>
</template>

<script>
	import dsbridge from '../assets/js/dsbridge.js';
  export default {
    name:'firstPopup',
	props:['item'],
    data() {
      return {
        state:true,
      }
    },
	mounted() {
		document.getElementsByTagName('body')[0].className = 'bodyhidden';
		let _this = this;
	},
	methods:{
		watchBanner(){
			let _this = this;
			var id = _this.item.slotId;
			_this.rewardVideoAdShow(id);
		},
		clousePopup(amount){
			let _this = this;
			var dataObj = {
				amount:amount == 1?_this.item.amount:amount
			}
			this.$emit("clouseFirstPopup",dataObj);
		},
		loadRewardVideoAd(code_id){//预加载激励视频
			let _this = this;
			var param = { code_id: code_id };
			dsBridge.call("lyh5x.fad.cachereward.load", param);
		},
		rewardVideoAdShow(code_id){//显示激励视频
			let _this = this;
			var param = { code_id: code_id };
			dsBridge.call("lyh5x.fad.cachereward.show", param,_this.neli1);
		},
		neli1(ret){
			let _this = this;
			console.log(ret);
			if (ret.event == "onError") {
			 } else if (ret.event == "onAdLoad") {
				console.log('onAdLoad=====first===',ret);
			 } else if (ret.event == "onAdShow") {
			 } else if (ret.event == "onAdClicked") {
			 } else if (ret.event == "onAdClosed") {
			 } else if (ret.event == "onVideoComplete") {
			 } else if (ret.event == "onReward") {
				 console.log('onReward=====first===',ret);
				_this.state = false;
			 }
		}
	},
	beforeDestroy() {
		document.getElementsByTagName('body')[0].className = 'bodyshow';
	}
  }
</script>

<style scoped>
  .firstPopup{
    width: 100%;
    height: 100vh;
    position: fixed;
    top:0;
    left:0;
    z-index: 99;
    background-color: rgba(0,0,0,0.7);
  }
  .firstPopup_box{
    width: 8.8rem;
    height: 11.06rem;
    background-image: url(../static/img/bg_popup_first01.png);
    background-repeat: no-repeat;
    background-size: 100%;
    margin:1rem auto 0 auto;
    box-sizing: border-box;
    padding:4.8rem 1.14rem 0.25rem 1.14rem;
  }
  .firstPopup_box.act{
    background-image: url(../static/img/bg_popup_first02.png);
  }
  .firstPopup_box>p{
    text-align: center;
  }
  .cumulative{
    font-size: 0.48rem;
    color:#787878;
  }
  .cumulative1{
	  font-size: 0.48rem;
	  color:#0078ff;
	  margin-top: 0.6rem;
  }
  .integral{
    font-size: 1.7rem;
    color:#fb4479;
    margin-top:0.3rem;
    font-weight: 600;
  }
  .text1{
    font-size: 0.48rem;
    color:#fb4479;
    margin-top:0.3rem;
    font-weight: 600;
  }
  .btn_box2{
    width: 100%;
    margin-top:0.5rem;
    text-align: center;
  }
  .btn_video,.btn_goSignin{
    width:6.36rem;
    height: 1.32rem;
    background-image: url(../static/img/btn_video.png);
    background-repeat: no-repeat;
    background-size: 100%;
    margin:0.3rem auto;
  }
  .btn_goSignin{
    background-image: url(../static/img/btn_goSignin.png);
  }
  .icon_close{
	  display: block;
	  width: 1rem;
	  height: 1rem;
	  margin:0.5rem auto;
	  background-image: url(../static/img/icon_close.png);
	  background-repeat: no-repeat;
	  background-size: 100%;
  }
</style>
