<template>
  <div class="popup">
    <div :class="doubleFlag?'popup_box act':'popup_box'">
      <p class="integral"><span class="text1">+</span>{{ nowState?item.amount:doubleAmount }}<span class="text1">积分</span></p>
      <div class="btn_box" v-if="nowState">
        <p class="btn_leave" @click="clousePopup(0)"></p>
        <p class="btn_double" @click="double"></p>
      </div>
      <div class="btn_box2" v-else>
        <p class="btn_popup" @click="clousePopup(1)"></p>
      </div>
    </div>

  </div>
</template>

<script>
	import dsbridge from '../assets/js/dsbridge.js';
  export default{
    name:'popup',
	props:['item'],
    data(){
      return {
        nowState:true,
		doubleAmount:1,
		doubleFlag:false
      }
    },
	mounted() {
		document.getElementsByTagName('body')[0].className = 'bodyhidden';
		let _this = this;
		console.log(_this.item);
		if('taskId' in _this.item){
			_this.doubleAmount = _this.item.amount
			_this.nowState = false;
		}else{
			_this.doubleAmount = _this.item.doubleAmount
		}
		if(_this.item.type == 'sPopup'){
			_this.nowState = false;
			_this.doubleFlag = true;
		}
	},
	methods:{
		double(){//展示双倍激励视频
			let _this = this;
			if('redPacketSlotId' in _this.item){
				_this.rewardVideoAdShow(_this.item.redPacketSlotId);
			}else{
				_this.rewardVideoAdShow(_this.item.slotId);
			}
		},
		clousePopup(isDouble){
			let _this = this;
			var type,db,id;
			if('id' in _this.item){
				type = 'redPackets';
				db = isDouble;
				id =_this.item.id;
			}else if('sort' in _this.item){
				type = 'bubble';
				db = isDouble;
				id =_this.item.sort;
			}else if('taskId' in _this.item){
				type = 'task';
				db = 0;
				id =_this.item.taskId;
			}else{
				type = 'popup';
				db = isDouble;
				id = '1';
			}
			
			var dataObj = {
				id:id,
				isDouble:db,
				type:type
			}
			_this.$emit("clousePopup",dataObj);
		},
		rewardVideoAdShow(code_id){//显示激励视频
			let _this = this;
			var param = { code_id: code_id };
			dsBridge.call("lyh5x.fad.cachereward.show", param,_this.neli3);
		},
		neli3(ret){
			let _this = this;
			console.log(ret);
			if (ret.event == "onError") {
			     var type = ret.type;//类型
			     var code = ret.code;//错误码
			     var msg = ret.msg;//错误信
			 } else if (ret.event == "onAdLoad") {
				console.log('onAdLoad=====popup===',ret);
			 } else if (ret.event == "onAdShow") {
			 } else if (ret.event == "onAdClicked") {
			 } else if (ret.event == "onAdClosed") {
			 } else if (ret.event == "onVideoComplete") {
			 } else if (ret.event == "onReward") {
			    var track_id = ret.track_id;//类型
				_this.nowState = false;
				_this.doubleFlag = true;
			 }
		}
	},
	beforeDestroy() {
		document.getElementsByTagName('body')[0].className = 'bodyshow';
	}
  }
</script>

<style scoped>
  .popup{
    width: 100%;
    height: 100vh;
    position: fixed;
    top:0;
    left:0;
    z-index: 99;
    background-color: rgba(0,0,0,0.7);
  }
  .popup_box{
    width: 8.80rem;
    height: 9.26rem;
    background-image: url(../static/img/bg_popup.png);
    background-repeat: no-repeat;
    background-size: 100%;
    margin:1rem auto 0 auto;
    box-sizing: border-box;
    padding:4.5rem 1.14rem 0.25rem 1.14rem;
  }
  .popup_box.act{
    background-image: url(../static/img/bg_popup_double.png);
  }
  .popup_box>p{
    text-align: center;
  }
  .cumulative{
    font-size:  0.48rem;
    color:#787878;
    margin-top:0.5rem;
  }
  .integral{
    font-size: 1.7rem;
    color:#fb4479;
    margin-top:0.3rem;
    font-weight: 600;
  }
  .text1{
    font-size: 0.48rem;
    color:#fb4479;
    margin-top:0.3rem;
    font-weight: 600;
  }
  .btn_box{
    display: flex;
    justify-content:space-around;
    width: 100%;
    margin-top:0.8rem;
  }
  .btn_leave,.btn_double{
    width:3.86rem;
    height: 1.32rem;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .btn_leave{
    background-image: url(../static/img/btn_leave.png);
	margin-right:0.2rem;
  }
  .btn_double{
    background-image: url(../static/img/btn_double.png);
  }
  .btn_box2{
    width: 100%;
    margin-top:0.7rem;
    text-align: center;
  }
  .btn_popup{
    width:5.6rem;
    height: 1.31rem;
    background-image: url(../static/img/btn_popup.png);
    background-repeat: no-repeat;
    background-size: 100%;
    margin:0.3rem auto;
  }
</style>
