function TimestampTpDate(timestamp){
	var ymdhss = new Date(timestamp).toLocaleString('chinese',{hour12:false}).replace(/\//g,'-')
	return ymdhss;
}

function formatDate (value,type) {
    if (typeof (value) == 'undefined') {
        return ''
    } else {
        let date = new Date(parseInt(value))
        let y = date.getFullYear()
        let MM = date.getMonth() + 1
        MM = MM < 10 ? ('0' + MM) : MM
        let d = date.getDate()
        d = d < 10 ? ('0' + d) : d
        let h = date.getHours()
        h = h < 10 ? ('0' + h) : h
        let m = date.getMinutes()
        m = m < 10 ? ('0' + m) : m
        let s = date.getSeconds()
        s = s < 10 ? ('0' + s) : s
		
		if(type == 'ymd'){
			return y + '-' + MM + '-' + d
		}else if( type == 'ymdhms'){
			return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s
		}else{
			return ''
		}
    }
}
Vue.prototype.$timestampTpDate = formatDate;
