<template>
  <div class="signin">
    <div class="signin_box">
      <p class="cumulative">已累计签到{{ day }}天</p>
      <p class="integral"><span class="text1">+</span>{{ amount }}<span class="text1">积分</span></p>
      <ul class="signin_list">
      	<li :class="items.state == 1?'act':''" v-for="(items,index) in item" :key="items.day">
		  <span v-if="items.state == 1">已签到</span>
          <span>{{ items.signAmount }}积分</span>
        </li>
      </ul>
      <div class="btn_box" v-if="nowState">
        <p class="btn_leave" @click="clousePopup(0)"></p>
        <p class="btn_double" @click="showVideo"></p>
      </div>
      <div class="btn_box1" v-else>
        <p class="btn_continue" @click="clousePopup(3)"></p>
        <p class="cumulative">今天已签到，明天再来吧</p>
      </div>
    </div>
  </div>
</template>

<script>
	import dsbridge from '../assets/js/dsbridge.js';
  export default {
    name:'signin',
	props:['item','nowState'],
    data() {
      return {
		nowData:{},
		amount:0,
		day:0,
      }
    },
	mounted() {
		document.getElementsByTagName('body')[0].className = 'bodyhidden';
		let _this = this;
		var c = 0;
		for(var i in _this.item){
			if(_this.item[i].state == 1){
				c++;
			}
		}
		console.log(_this.item);
		_this.nowData = _this.item[c-1];
		var signinAmount = localStorage.getItem("signinAmount");
		_this.amount = signinAmount || _this.item[c-1].signAmount;
		_this.day = c;
	},
	methods:{
		showVideo(){
			let _this = this;
			var id = _this.nowData.signSlotId;
			_this.rewardVideoAdShow(id);
		},
		clousePopup(isdouble){
			let _this = this;
			var dataObj = {
				isDouble:isdouble,
				signAmount:_this.nowData.signAmount,
				doubleAmount:_this.nowData.doubleAmount
			}
			_this.$emit("clouseSigninPopup",dataObj);
		},
		rewardVideoAdShow(code_id){//显示激励视频
			let _this = this;
			var param = { code_id: code_id };
			dsBridge.call("lyh5x.fad.cachereward.show", param,_this.neli2);
		},
		neli2(ret){
			let _this = this;
			console.log(ret);
			if (ret.event == "onError") {
			     var type = ret.type;//类型
			     var code = ret.code;//错误码
			     var msg = ret.msg;//错误信
			 } else if (ret.event == "onAdLoad") {
				console.log('onAdLoad=====signin===',ret);
			 } else if (ret.event == "onAdShow") {
			 } else if (ret.event == "onAdClicked") {
			 } else if (ret.event == "onAdClosed") {
			 } else if (ret.event == "onVideoComplete") {
			 } else if (ret.event == "onReward") {
				 console.log('onReward=====signin===',ret);
			    _this.clousePopup(1);
			 }
		}
	},
	beforeDestroy() {
		document.getElementsByTagName('body')[0].className = 'bodyshow';
	}
  }
</script>

<style scoped>
  .signin{
    width: 100%;
    height: 100vh;
    position: fixed;
    top:0;
    left:0;
    z-index: 99;
    background-color: rgba(0,0,0,0.7);
  }
  .signin_box{
    width: 8.80rem;
    height: 14.25rem;
    background: url(../static/img/bg_popup_signin.png) no-repeat;
    background-size: 100%;
    margin:1rem auto 0 auto;
    box-sizing: border-box;
    padding:4.5rem 0.4rem 0.25rem 0.4rem;
  }
  .signin_box>p{
    text-align: center;
  }
  .cumulative{
    font-size:  0.48rem;
    color:#787878;
  }
  .integral{
    font-size: 1.7rem;
    color:#fb4479;
    margin-top:0.3rem;
    font-weight: 600;
  }
  .text1{
    font-size: 0.48rem;
    color:#fb4479;
    margin-top:0.3rem;
    font-weight: 600;
  }
  .signin_list{
    display: flex;
    flex-wrap:wrap;
    width: 100%;
  }
  .signin_list li{
    width: 1.8rem;
    height: 1.8rem;
    background: url(../static/img/bg_wqd.png) no-repeat;
	background-size: 100%;
    border-radius: 0.1rem;
    overflow: hidden;
    margin:0.12rem 0.1rem;
    position: relative;
  }
  .signin_list li:last-child{
	  width: 3.8rem;
	  box-sizing: border-box;
	  padding-left: 2rem;
	  padding-top:0.5rem;
	  background: url(../static/img/bg_wqd_7.png) no-repeat;
	  background-size: 100%;
  }
  .signin_list li.act{
    background: url(../static/img/bg_yqd.png) no-repeat;
    background-size: 100%;
  }
  .signin_list li:last-child.act{
  	  background: url(../static/img/bg_yqd_7.png) no-repeat;
  	  background-size: 100%;
  }
  .signin_list li span{
    display: block;
    width: 100%;
    text-align: center;
    font-size: 0.30rem;
    color:#fff;
    line-height: 0.48rem;
  }
  .signin_list li img{
    display: block;
    width: 0.7rem;
    height: 0.7rem;
    margin: 0 auto;

  }
  .signin_list li img.icon_diamonds{
    width: 0.84rem;
    height: 0.72rem;
  }
  .btn_box{
    display: flex;
    justify-content:space-around;
    width: 100%;
    margin-top:0.5rem;
  }
  .btn_leave,.btn_double{
    width:3.86rem;
    height: 1.32rem;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .btn_leave{
    background-image: url(../static/img/btn_leave.png);
  }
  .btn_double{
    background-image: url(../static/img/btn_double.png);
  }
  .btn_box1{
    width: 100%;
    margin-top:0.05rem;
    text-align: center;
  }
  .btn_continue{
    width:6.36rem;
    height: 1.32rem;
    background-image: url(../static/img/btn_continue.png);
    background-repeat: no-repeat;
    background-size: 100%;
    margin:0.5rem auto;
  }

</style>
